    freshOn() {
      this.fresh = true
    },
    freshOff() {
      this.fresh = false
    },
    logincheck() {
      // id.preventDefault()
      if (this.$store.state.userDb.id === this.id) {
        let obj = {}
        localStorage.id = this.id
        localStorage.pw = this.pw
        localStorage.status = "ok"
        this.$router.push("/update_discounted_vehicle_in_list")
        this.$store.commit("isLoginSuccess")
        console.log("login_info1 : ", obj)
        this.sendMessage(obj)
      }
    }

    // socket_process(data) {
    //   if (data.kind === "login" && data.status === "ok") {
    //     console.log("login success")
    //     localStorage.id = data.doc.id
    //     localStorage.pw = data.doc.pw
    //     localStorage.status = "ok"
    //     let obj = {}
    //     obj.id = data.doc.id
    //     obj.pw = data.doc.pw
    //     obj.status = "ok"
    //     this.$store.dispatch("login", obj)
    //   }
    // }
    // menuHover(e) {
    //   let y = e.clientY
    //   let ele = document.getElementById("header")
    //   if (y < 100) {
    //     ele.classList.add("headroom--down")
    //   }
    // }
  },
  created() {
    window.addEventListener("scroll", this.handleScroll),
      this.$socket.on("message_from_server", data => {
        console.log("data.kind : ", data)
        this.socket_process(data)
      }) //,
    //
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll)
  }
}
