<template>
  <div
    id="header"
    :class="{ showHight: fresh === true }"
    v-if="loginSuccess === true"
  >
    <div
      class="header_area"
      :class="{ showHight: fresh === true }"
      @mouseleave="mouseleaveEvent()"
    >
      <div
        class="header_system"
        @mouseover="mouseoverEvent()"
        :class="{ disable: fresh === true }"
      >
        <div class="infor_navi">
          <v-btn icon @click="logout1()">
            <v-icon>mdi-logout</v-icon>로그아웃
          </v-btn>
          <v-btn icon @click="openChangePwd()">
            <span>
              <font-awesome-icon icon="user-cog" /> {{ id }} 개인정보
              수정
            </span>
          </v-btn>
        </div>
      </div>

      <!-- 비밀번호 -->
      <!--
      <div class="content_wrap">
        <div class="display_popup popup" v-show="infoPop">
          <div>
            <h3><font-awesome-icon icon="user" /> 비밀번호 수정</h3>
            <button @click="closechangePwd">
              <font-awesome-icon icon="times-circle" />
            </button>
            <div>
              <span>현재 비밀번호 : </span
              ><input type="password" v-model="pw" /> <br />
              <span>비밀번호 변경 : </span><input type="password" /> <br />
              <span>비밀번호 확인 : </span><input type="password" />
            </div>
            <button @click.prevent="registNew" class="writebtn">
              수정완료
            </button>
          </div>
        </div>
      </div>
-->
      <header
        class="header-menu"
        :class="{ on: fresh }"
        @mouseleave="mouseleaveEvent"
      >
        <div class="header-content">
          <router-link :to="{ name: 'live_vehicle' }"
            ><img src="@/assets/logo_top.png" alt=""
          /></router-link>
          <ul class="header_nav">
            <li>
              <a
                href="javascript:void(0)"
                @click="isHeaderNav2()"
                :class="{ active: headerNav2 === true }"
                ><span> 운영기능</span></a
              >
              <ul class="nav_sub_depth" :class="{ on: headerNav2 }">
                <li>
                  <router-link :to="{ name: 'pay_for_discounted_vehicle_list' }"
                    ><span> 입주사 방문할인</span></router-link
                  >
                </li>
           <!--     <li>
                  <router-link :to="{ name: 'manage_visit_reservation' }"
                    ><span>방문 예약 차량 관리</span>
                  </router-link>
                </li> -->
              </ul>
            </li>
            <li>
              <a
                href="javascript:void(0)"
                @click="isHeaderNav3()"
                :class="{ active: headerNav3 === true }"
                ><span> 조회기능</span></a
              >
              <ul class="nav_sub_depth" :class="{ on: headerNav3 }">
                <li>
                  <router-link
                    :to="{ name: 'update_discounted_vehicle_in_list' }"
                    ><span> 방문차량 할인(입차)</span></router-link
                  >
                </li>
                <li>
                  <router-link
                    :to="{ name: 'update_discounted_vehicle_out_list' }"
                    ><span> 방문차량 할인(출차)</span></router-link
                  >
                </li>               
              </ul>
            </li>
          </ul>
        </div>
      </header>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex"
export default {
  data() {
    return {
      limitPosition: 0,
      lastPosition: 300,
      scrolled: false,
      id: "",
      pw: "",
      status: "",
      headerNav: false,
      headerNav2: false,
      headerNav3: false,
      headerNav4: false,
      window: { width: 0, height: 0 },
      infoPop: false
    }
  },
  computed: {
    ...mapState(["loginSuccess", "loginError", "userGetDb", "fresh"])
  },
  created() {
    window.addEventListener("resize", this.handleResize)
    this.handleResize()
    this.id = localStorage.id
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize)
  },
  watch: {
    $route: "freshOff"
  },
  methods: {
    ...mapActions(["login", "logout", "freshOn", "freshOff", "setup"]),
    login_check(id) {
      let obj = {}
      obj.id = id.id
      obj.pw = id.pw
      obj.kind = "resident_login"
      this.sendMessage(obj)
    },
    sendMessage(c) {
      this.$socket.emit("message_from_web_client", c)
    },
    logout1(){
  let obj = {}
      obj.kind = "residnet_logout"
      obj.id = localStorage.id
      console.log("logout : ", obj)
      this.sendMessage(obj)
      this.$store.dispatch("logout")
    },   
    socket_process(data) {
      if (data.kind === "resident_login" && data.status === "ok") {
        console.log("resident login success")
        localStorage.id = data.id
        localStorage.pw = data.pw
        localStorage.status = "ok"
        let obj = {}
        obj.id = data.doc.id
        obj.pw = data.doc.pw
        obj.status = "ok"
        this.$store.dispatch("login", obj)
      }
    },
    openChangePwd() {
      this.infoPop = true
      this.$store.state.pwInfo = true
    },
    closechangePwd() {
      this.infoPop = false
    },
    mouseEnterEvent() {
      if (this.fresh === false) {
        this.freshOn()
      } else {
        this.freshOff()
      }
    },
    mouseoverEvent() {
      if (this.window.width > 1024) {
        this.freshOn()
      }
    },
    mouseleaveEvent() {
      if (this.window.width > 1024) {
        this.freshOff()
      }
    },
    handleResize() {
      this.window.width = window.innerWidth
      this.window.height = window.innerHeight
    },
    isHeaderNav() {
      this.headerNav = !this.headerNav
      this.headerNav2 = false
      this.headerNav3 = false
      this.headerNav4 = false
    },
    isHeaderNav2() {
      this.headerNav = false
      this.headerNav2 = !this.headerNav2
      this.headerNav3 = false
      this.headerNav4 = false
    },
    isHeaderNav3() {
      this.headerNav = false
      this.headerNav2 = false
      this.headerNav3 = !this.headerNav3
      this.headerNav4 = false
    },
    isHeaderNav4() {
      this.headerNav = false
      this.headerNav2 = false
      this.headerNav3 = false
      this.headerNav4 = !this.headerNav4
    }
  }
}
</script>
<style>
#header {
  position: fixed;
  height: 95px;
  width: 100%;
  z-index: 200;
}
.showHight {
  height: 100% !important;
}
#header .header-menu {
  border-bottom: 1px solid #d5d5d5;
  opacity: 0;
  transition: all 0.2s ease;
  margin-top: 0px;
  height: 30px !important ;
  position: relative;
  z-index: 40;
  top: -30px;
}
.header_area:hover .header-menu {
  display: block !important;
}
#header .header-menu.on {
  opacity: 1;
  margin-top: 0px;
  height: 65px !important;
  display: block;
  top: 0px;
}
#header .header-menu.on::before {
  border-bottom: 1px solid #d5d5d5;
  width: 100%;
  content: "";
  display: block;
  clear: both;
  position: relative;
  z-index: 10;
  top: 64px;
}
#conents-wrap {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto 0;
  position: relative;
  z-index: 1;
  padding: 35px 0 0px;
  transition: all 0.3s linear;
  height: 100%;
}

/* 상단메인네비 */
header {
  background: #fff;
}
#header .header_area {
  margin: 0 auto;
  position: relative;
  z-index: 100;
  transition: all 0.2s ease;
  height: auto;
}
#header .header_system {
  margin: 0 auto;
  background: #29a95c;
  width: 100%;
  position: relative;
  z-index: 50;
}
#header .header_system .infor_navi {
  background: #29a95c !important;
  height: 30px !important;
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#header .header_system .infor_navi .nav-open-btn {
  display: none;
  position: relative;
  height: 20px;
  margin-left: 4%;
}
#header .header_system .infor_navi .nav-open-btn span {
  width: 25px;
  height: 0.2rem;
  background: #fff;
  display: block;
}
#header .header_system .infor_navi .nav-open-btn span:before,
#header .header_system .infor_navi .nav-open-btn span:after {
  width: 25px;
  height: 0.2rem;
  background: #fff;
  content: "";
  clear: both;
  display: block;
  position: absolute;
  top: 0;
  transform: translate(0, 10px);
  transition: all 0.3s ease-out;
}
#header .header_system .infor_navi .nav-open-btn span:after {
  transform: translate(0, 20px);
}
#header .header_system.disable .infor_navi .nav-open-btn span {
  background: transparent;
  margin-top: 5px;
}
#header .header_system.disable .infor_navi .nav-open-btn span:after {
  transform: rotate(225deg) translate(-4px, -8px);
  width: 27px;
}
#header .header_system.disable .infor_navi .nav-open-btn span:before {
  transform: rotate(-225deg) translate(9px, -3px);
  width: 27px;
}
#header .header_system .infor_navi > span {
  color: #fff;
  margin-left: 2%;
}
#header .header_system .infor_navi button {
  color: white !important;
  margin-right: 10px;
  width: auto;
}
#header .header_system .infor_navi button:last-child {
  margin: 0 !important;
}
#header .header_system .infor_navi button i {
  color: white !important;
}
#header .header-menu .header-content {
  padding: 0 !important;
  width: 100% !important;
  margin: 0 auto;
  max-width: 1280px;
  position: relative !important;
  align-items: center;
  display: none;
}
#header .header-menu.on .header-content {
  display: flex !important;
  margin-top: -1px;
}
#header .header-menu.on .header-content .nav-close-btn {
  position: absolute;
  font-size: 40px;
  right: 0;
  top: 0;
  margin: 1.5em;
  display: none;
  color: #fff;
}
#header header ul.header_nav {
  text-align: center;
  width: 100%;
  padding-left: 86px;
  height: 65px;
  line-height: 65px;
}
#header header ul.header_nav > li {
  width: 25%;
  display: inline-block;
  font-weight: 500;
  font-size: 20px;
  position: relative;
}

#header header ul.header_nav > li a span {
  cursor: pointer;
  letter-spacing: -0.3px;
  display: block;
}
#header header ul.header_nav > li > a:hover {
  color: #28a95c;
  font-weight: 500;
}
#header header ul.header_nav > li:last-child ul {
  border-right: none;
}
#header header ul.header_nav ul.nav_sub_depth {
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 104px;
  padding: 2px 0;
  border-right: 1px solid #c5c5c5;
  overflow: hidden;
}
#header header ul.header_nav ul.nav_sub_depth li {
  color: #666;
  font-weight: normal;
  text-align: center;
  line-height: 30px;
  padding: 0 10px;
  font-size: 16px;
}
.router-link-active {
  color: #ffcc80 !important;
}
#header header ul.header_nav ul.nav_sub_depth li a {
  color: #fff;
}
#header header ul.header_nav ul.nav_sub_depth li a:hover {
  color: #ffcc80;
  text-decoration-line: underline;
}
#header .header-menu.on::after {
  background: #35996c;
  position: absolute;
  width: 100%;
  left: 0;
  z-index: -1;
  height: 105px;
  content: "";
  opacity: 0.95;
  top: 64px;
}

/*  ///////////////////기기별사이즈 ///////////////////          */
@media screen and (max-width: 1024px) {
  #header .header_system {
    padding: 0 20px !important;
    z-index: 0;
  }
  #header .header-menu.on .header-content {
    margin-top: 0;
  }
  #header .header_system.disable {
    /* display: none; */
    position: absolute;
    /* z-index: -1; */
  }
  #header .header-menu {
    z-index: -1;
    /* top: -60px; */
    right: -100%;
    height: 0;
    display: none;
  }
  #header .header-menu.on::after {
    content: "";
    display: block;
    clear: both;
    height: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 500;
    width: 100%;
    background: url(/img/bg.a891cfc1.jpg);
    background-color: rgb(211, 211, 211);
  }
  #header .header-menu.on .header-content .nav-close-btn {
    display: block;
  }

  #header .header-menu.on {
    height: 100% !important;
    right: 0;
  }
  #header .header-menu.on:before {
    width: 100%;
    height: 100%;
    background: #474747;
    opacity: 0.4;
    display: block;
    content: "";
    clear: both;
    position: absolute;
  }
  #header .header-menu.on .header-content {
    height: 100% !important;
    padding: 2rem 2rem 0 !important;
    display: block !important;
    z-index: 600;
  }
  #header .header-menu.on .header-content > a {
    display: inline-block;
    padding: 5em 3em 1em;
  }

  #sub_wrap .breadcrumbs {
    display: none;
  }
  #conents-wrap {
    padding: 65px 20px 0px;
  }
  #header .header_system .infor_navi {
    height: 60px !important;
  }
  #header .header_system .infor_navi .nav-open-btn {
    display: block;
  }
  #header header ul.header_nav {
    padding-left: 40px;
    height: auto;
    padding: 3em;
  }
  #header header ul.header_nav > li {
    display: flex;
    align-items: center;
    width: auto;
    border-bottom: 1px solid #d5d5d5;
  }
  #header header ul.header_nav li a {
    color: #fff;
    width: 20%;
  }
  #header .header-menu.on ul.header_nav ul.nav_sub_depth {
    display: flex;
    position: relative;
    align-items: center;
    margin-left: 10%;
    background: none;
    border-right: none;
    height: 130px;
  }
  #header header ul.header_nav > li > a.active {
    color: #28a95c !important;
  }
  /* 테블릿 */
}
@media screen and (max-width: 768px) {
  .router-link-exact-active {
    color: #29a95c !important;
  }
  #header header ul.header_nav > li {
    display: block;
    text-align: left;
    border: none;
  }
  #header .header-menu.on ul.header_nav ul.nav_sub_depth.on {
    opacity: 1;
    height: auto;
    margin-top: 0;
    /* transition: all 0.3s ease; */
    visibility: visible;
  }
  #header .header-menu.on ul.header_nav ul.nav_sub_depth {
    margin-left: 0;
    display: none;
    height: auto;
    border-right: none;
    /* opacity: 0; */
    /* transition: all 0.3s ease; */
    display: block;
    visibility: hidden;
    height: 1px;
  }
  #header .header-menu.on ul.header_nav ul.nav_sub_depth li a:hover {
    color: #fff !important;
  }
  #header header ul.header_nav > li > a {
    width: 100%;
    border-bottom: 1px solid #d5d5d5;
    display: block;
    position: relative;
    transition: all 0.5s ease;
  }
  #header header ul.header_nav > li > a:before {
    position: absolute;
    top: 50%;
    right: 42px;
    /* -webkit-transform: translateY(-50%); */
    transform: translateY(-50%);
    width: 2px;
    height: 0px;
    background: #fff;
    content: "";
    /* -webkit-transition: height 0.3s; */
    transition: height 0.1s;
  }
  #header header ul.header_nav > li > a.active:before {
    height: 20px;
  }
  #header header ul.header_nav > li > a:hover {
    /* pointer-events: none !important; */
  }
  #header header ul.header_nav > li > a:after {
    position: absolute;
    top: 50%;
    right: 42px;
    -webkit-transform: translateX(50%);
    transform: translateX(50%);
    width: 20px;
    height: 2px;
    background: #fff;
    content: "";
  }
  #header header ul.header_nav ul.nav_sub_depth li {
    text-align: left;
    padding: 8px;
    margin-top: 1px;
    background: rgba(50, 50, 50, 0.3);
    line-height: normal;
    height: 1px;
    transition: all 0.2s cubic-bezier(0.65, 0.48, 0.22, 1.1);
    opacity: 0;
  }
  #header header ul.header_nav ul.nav_sub_depth.on li {
    height: 42px;
    opacity: 1;
  }
}
/* 모바일 */
@media (min-width: 320px) and (max-width: 480px) {
  #header .header-menu.on .header-content {
    padding: 2rem 0rem 0 !important;
  }
  #header header ul.header_nav {
    padding: 1em;
  }
  #header .header_system .infor_navi span {
    font-size: 12px;
  }
  #header .header-menu.on .header-content > a {
    padding: 4em 1em 0em;
  }
  #header .header-menu.on .header-content > a img {
    width: 70%;
  }
  #header header ul.header_nav > li {
    font-size: 15px;
  }
  #header header ul.header_nav ul.nav_sub_depth.on li {
    font-size: 13px;
  }
  #header header ul.header_nav > li > a.active:before {
    height: 16px;
  }
  #header header ul.header_nav > li > a:before {
    top: 52%;
    right: 41px;
  }
  #header header ul.header_nav > li > a:after {
    width: 16px;
  }
}
</style>
