<template
  ><div class="body_wrap" :class="{ bodyWrap: loginSuccess === false }">
    <CHANGINGINPO v-if="$store.state.pwInfo"></CHANGINGINPO>

    <loginForm v-if="loginSuccess === false" />
    <mainNavigation />
    <div id="conents-wrap" v-if="loginSuccess">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex"
import mainNavigation from "./components/mainNavigation.vue"
import loginForm from "./components/loginform.vue"
import CHANGINGINPO from "./components/CHANGINGINPO.vue"
export default {
  data() {
    return {}
  },
  components: {
    mainNavigation,
    loginForm,
    CHANGINGINPO
  },
  computed: {
    ...mapState(["loginSuccess", "loginError", "userGetDb", "pwInfo"])
  },
  methods: {
    ...mapActions(["login", "logout"]),
    start_login_check() {
      console.log("localStorage.status :", localStorage.status)
      if (localStorage.status !== "ok") {
        this.$store.dispatch("logout")
      } else {
        let obj = {}
        obj.id = localStorage.id
        obj.pw = localStorage.pw
        obj.status = localStorage.status
        console.log("start_login_check : ", obj)
        this.$store.dispatch("login", obj)
      }
    }
  },
  created() {
    window.addEventListener("scroll", this.handleScroll),
      this.start_login_check()
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll)
  }
}
</script>
<style>
/* @import "./assets/css/divice.css"; */
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
.body_wrap {
  height: 100%;
  position: relative;
  /* min-height: 800px; */
}
.bodyWrap {
  height: 100% !important;
  overflow-y: hidden;
}
ul,
li,
a {
  list-style: none;
  color: #666;
  text-decoration: none;
}
dl,
dt,
dd {
  margin: 0;
}
button,
input {
  outline: none;
}
td,
th {
  font-weight: normal;
}
.loginform_wrap input,
.loginform_wrap label,
.loginform_wrap fieldset,
.loginform_wrap input::-webkit-input-placeholder,
.loginform_wrap input::placeholder {
  color: white !important;
}
#sub_wrap {
  height: 100%;
}
#sub_wrap .breadcrumbs {
  font-size: 13px;
  height: 2%;
}
#sub_wrap .breadcrumbs li {
  display: inline-block;
  color: #bababa;
}
#sub_wrap .breadcrumbs li:before {
  color: #cfd0d2;
  font-size: 13px;
  font-weight: 200;
  content: ">";
  padding: 0 6px;
}
#sub_wrap .breadcrumbs ul li:first-child {
  text-indent: -9999px;
  width: 12px;
  height: 12px;
  background-image: url(./assets/home-run.png);
  background-repeat: no-repeat;
  background-position: left center;
  line-height: 14px;
}
#sub_wrap .breadcrumbs ul li:last-child {
  color: #666;
  font-size: 13px;
}

@import "./assets/css/layout.css";
</style>
