import Vue from "vue"
import Vuex from "vuex"
import router from "@/router"
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userGetDb: {},
    userDb: { id: "admin001", pw: "1234", status: "ok" },
    loginSuccess: false,
    loginError: false,
    fresh: false,
    pwInfo: false
  },
  mutations: {
    isLoginSuccess(state, payload) {
      console.log("isLoginSuccess : ", payload)
      state.loginSuccess = true
      state.loginError = false
      this.$router.push("/update_discounted_vehicle_in_list")
      state.userGetDb.id = payload.id
      state.userGetDb.pw = payload.pw
      state.userGetDb.status = "ok"
    },
    isLoginError(state) {
      // localStorage.admin_id = ""
      // localStorage.admin_pw = ""
      // localStorage.admin_status = ""
      state.loginSuccess = false
      state.loginError = true
    },
    logout(state) {
      console.log("logout")
      localStorage.id = ""
      localStorage.pw = ""
      localStorage.status = ""
      state.loginSuccess = false
      state.loginError = false
      state.userGetDb = {}
      // router.push({ name: "login" })
    },
    freshOn(state) {
      state.fresh = true
    },
    freshOff(state) {
      state.fresh = false
    },
    changingInfo(state) {
      state.pwInfo = true
    },
    popupClose(state) {
      state.pwInfo = false
    }
  },
  actions: {
    login({ commit }, payload) {
      console.log("payload : ", payload)
      commit("isLoginSuccess", payload)
      router.push({ name: "pay_for_discounted_vehicle_list" })
      console.log("login 성공")
    },
    logout({ commit }) {
      commit("logout")
      router.push({ name: "login" })
    },
    freshOn({ commit }) {
      commit("freshOn")
    },
    freshOff({ commit }) {
      commit("freshOff")
    },
    changingInfo({ commit }) {
      commit("changingInfo")
      // state.setupPopup = true
    },
    popupClose({ commit }) {
      commit("popupClose")
    }
  },
  modules: {}
})
