import Vue from "vue"
import VueRouter from "vue-router"
import store from "@/store"
Vue.use(VueRouter)

const checkLogin = (to, from, next) => {
  if (store.loginSuccess === true) {
    alert("로그인을 해야합니다.")
    next("/")
  } else {
    next("/")
    store.state.fresh = false
  }
}

const routes = [
  {
    path: "/",
    name: "login",
    component: () => import(/* webpackChunkName: "login" */ "../Login.vue")
  },

  {
    path: "/pay_for_discounted_vehicle_list",
    name: "pay_for_discounted_vehicle_list",
    beforeEnter: checkLogin,
    component: () =>
      import(
        /* webpackChunkName: "pay_for_discounted_vehicle_list" */ "../views/pay_for_discounted_vehicle_list.vue"
      )
  },
  {
    path: "/update_discounted_vehicle_in_list",
    name: "update_discounted_vehicle_in_list",
    component: () =>
      import(
        /* webpackChunkName: "update_discounted_vehicle_in_list" */ "../views/update_discounted_vehicle_in_list.vue"
      )
  },
  {
    path: "/update_discounted_vehicle_out_list",
    name: "update_discounted_vehicle_out_list",
    component: () =>
      import(
        /* webpackChunkName: "update_discounted_vehicle_out_list" */ "../views/update_discounted_vehicle_out_list.vue"
      )
  },
  {
    path: "/review_discounted_vehicle_out_list",
    name: "review_discounted_vehicle_out_list",
    component: () => import("../views/review_discounted_vehicle_out_list")
  },
  {
    path: "/manage_visit_reservation",
    name: "manage_visit_reservation",
    component: () => import("../views/manage_visit_reservation")
  },
  {
    path: "/resident_settlement",
    name: "resident_settlement",
    component: () => import("../views/resident_settlement")
  },
  {
    path: "/month_settlement",
    name: "month_settlement",
    component: () => import("../views/month_settlement")
  },
  {
    path: "/day_settlement",
    name: "day_settlement",
    component: () => import("../views/day_settlement")
  }
]

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
})

export default router
